<template>
  <v-container fluid class="col-12 my-3">
    <v-row>
      <v-card width="90%" class="mx-auto pb-10" elevation="10">
        <v-list-item>
          <v-list-item-content>
            <v-list-item-title class="text--store">Resultados de {{ search }} en todas las tiendas</v-list-item-title>
          </v-list-item-content>
        </v-list-item>

        <v-row>
          <v-col class="mx-auto" cols="12" lg="5" md="7" sm="7">
            <v-autocomplete
              :height="50"
              v-model="model"
              :loading="isLoading"
              :search-input.sync="newSearch"
              @keyup.enter="submit($event, newSearch)"
              outlined
              :cache-items="false"
              clearable
              hide-details
              hide-selected
              hide-no-data
              label="Busca cualquier producto..."
              append-icon
              prepend-inner-icon="mdi-magnify"
              solo
            >
              <!-- :items="dataResultsSearchByStore"
                item-text="name"
              item-value="id"-->
              <template v-slot:item="{ item }">
                <v-list-item-content>
                  <v-list-item-title v-text="item.name"></v-list-item-title>
                </v-list-item-content>
                <v-list-item-icon>
                  <v-icon>mdi-chevron-right</v-icon>
                </v-list-item-icon>
              </template>
            </v-autocomplete>
          </v-col>
        </v-row>
      </v-card>
    </v-row>
    <v-row>
      <v-col cols="12" v-if="verifiedResult">
        <v-card width="90%" class="mx-auto" elevation="10">
          <v-card-title>
            <span>No se encontro ningun resultado, busca de nuevo por favor</span>
          </v-card-title>
        </v-card>
      </v-col>
      <v-col v-for="item in dataResultsSearchByStore" :key="item.id" class="mx-auto" cols="10">
        <carousel-products
          @reloadData="reloadData()"
          :showButtonMore="false"
          :productsArray="item.products"
          :toNavigated="item.to"
          :titleCard="item.name"
        ></carousel-products>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { mapActions, mapState } from "vuex";
import CarouselProducts from "@/components/Products/CarouselProducts";

export default {
  name: "Stores",
  components: {
    carouselProducts: CarouselProducts
  },
  computed: {
    ...mapState("products", ["dataResultsSearchByStore"]),
    verifiedResult() {
      this.flag = false;
      this.counterProducts = 0;
      this.dataResultsSearchByStore.forEach(element => {
        this.counterProducts = this.counterProducts + element.products.length;
      });
      if (this.counterProducts == 0) {
        this.flag = true;
      }

      return this.flag;
    }
  },
  async mounted() {
    await this.reloadData();
  },
  data() {
    return {
      flag: false,
      counterProducts: 0,
      search: "",
      newSearch: "",
      isLoading: false,
      model: null
    };
  },
  methods: {
    ...mapActions("products", ["getProductsByStoreSearch"]),
    reloadData() {
      if (this.$route.query.input_search) {
        this.getProductsByStoreSearch(this.$route.query.input_search);
        this.search = this.$route.query.input_search;
      }
    },
    submit(event) {
      event.target.blur();
      this.$router
        .push({ name: "results", query: { input_search: this.newSearch } })
        .catch(err => err);
    }
  },
  watch: {
    $route(to, from) {
      this.reloadData();
    }
  }
};
</script>

<style>
.text--store {
  color: black;
  text-align: left;
  font-size: 35px;
  font-weight: 700;
  text-transform: initial;
  white-space: normal;
}
</style>